body {
  color: #222;
  background: #f7f8fa;
  margin: 0;
  font-family: system-ui, Arial, sans-serif;
}

input, button {
  border: 1px solid #ccc;
  border-radius: 4px;
  margin: .2em .4em .2em 0;
  padding: .5em;
  font-size: 1rem;
}

button {
  color: #fff;
  cursor: pointer;
  background: #1976d2;
  border: none;
  transition: background .2s;
}

button:hover {
  background: #1565c0;
}

form {
  margin-bottom: 1em;
}
